import React, { useCallback, useMemo, useContext } from "react";
import styled from "styled-components";
import oc from "open-color";
import moment from "moment";
import lodash from "lodash";

import { Profile, Layout } from "../components";
import { RootContext } from "../contexts/rootContext";

const Section = styled.section`
  padding: 25px 0;

  & + & {
    padding-top: 0;
  }

  & > ul {
    list-style: none;
    margin: 0;
  }

  li + li {
    margin-top: 0.5rem;
  }
`;

const SubTitle = styled.h2`
  color: ${oc.gray[8]};
`;

const ListHeader = styled.li`
  font-weight: bold;
  margin-bottom: 0.5rem;
`;

const DateLabel = styled.span`
  display: inline-block;
  margin-left: 0.5rem;
  color: ${oc.gray[6]};
  font-weight: normal;
`;

const List = styled.ul`
  list-style: none;
  margin: 0;
`;

const ListItem = styled.div`
  display: flex;

  & + & {
    margin-top: 1rem;
  }
`;

const SubList = styled.ul`
  font-size: 14px;

  li + li {
    margin-top: 0.3rem;
  }
`;

const UnivLogo = styled.img`
  width: 42px;
  height: 42px;
  margin-right: 1.5rem;
  margin-bottom: 0;
  border-radius: 3px;
`;

const Experiences = React.memo<{ profile: Profile }>(({ profile }) => {
  const getDateRange = useCallback((startDate: string, endDate: string) => {
    const endMoment = endDate === "Present" ? moment() : moment(endDate);
    const months = endMoment.diff(moment(startDate, "MMM, YYYY"), "M") + 1;
    if (months >= 12) {
      const yrs = Math.floor(months / 12);
      const mos = months % 12;
      return mos > 0 ? `${yrs} yrs ${mos} mos` : `${yrs}`;
    }
    return `${months} mos`;
  }, []);

  return (
    <Section>
      <SubTitle>Work experiences</SubTitle>
      <ul>
        {profile.experiences.map((item, index) => (
          <ListItem>
            <UnivLogo src={item.image} />
            <div>
              <ListHeader key={`${item.name}__${index}`}>
                {item.name}

                <DateLabel>
                  {lodash.last(item.histories)?.start_date} ~{" "}
                  {item.histories[0].end_date}
                  {`(${getDateRange(
                    lodash.last(item.histories)?.start_date ?? "",
                    item.histories[0].end_date
                  )})`}
                </DateLabel>
              </ListHeader>
              <SubList>
                {item.histories.map(history => (
                  <li key={history.role}>
                    {history.role}
                    {item.histories.length > 1 && (
                      <DateLabel>
                        {history.start_date} ~ {history.end_date}
                        {`(${getDateRange(
                          history.start_date,
                          history.end_date
                        )})`}
                      </DateLabel>
                    )}
                  </li>
                ))}
              </SubList>
            </div>
          </ListItem>
        ))}
      </ul>
    </Section>
  );
});

const About = React.memo(() => {
  const { state: rootState } = useContext(RootContext);
  const { profile } = rootState;
  if (!profile) {
    return null;
  }

  return (
    <Layout>
      <Profile />
      <Section>
        <SubTitle>Educations</SubTitle>
        <List>
          {profile.educations.map((item, index) => (
            <ListItem>
              <UnivLogo src={item.image} />
              <div>
                <ListHeader key={`${item.name}__${index}`}>
                  {item.name}
                  <DateLabel>
                    {item.start_date} ~ {item.end_date}
                  </DateLabel>
                </ListHeader>
                <SubList>
                  {item.major.map(major => (
                    <li key={major}>{major}</li>
                  ))}
                </SubList>
              </div>
            </ListItem>
          ))}
        </List>
      </Section>
      <Experiences profile={profile} />
    </Layout>
  );
});

export default About;
